<template>
  <div id="login">
    <!-- <login-nav-bar class="navbar" /> -->
    <section class="aui-flexView">
      <section class="aui-scrollView">
        <!---->
        <div class="aui-account-title">
          <h1></h1>
        </div>
        <!---->
        <div class="aui-account-tile">
          <div class="b-line">
            <input
              type="text"
              v-model="loginName"
              placeholder=""
              id="login_name"
            />
            <label id="login_name_text" class="err"></label>
          </div>
          <div>
            <input
              type="password"
              v-model="loginPassword"
              placeholder=""
              id="login_password"
            />
            <label class="err"></label>
          </div>
        </div>
        <!---->
        <div class="aui-account-button">
          <button id="login_submit" @click="login"></button>
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import md5 from "js-md5";

import { accountLogin} from "network/login";

export default {
  components: {},
  data() {
    return {
      loginName: "lisa",
      loginPassword: "nawxs2..2",
    };
  },
  methods: {
    login() {
      let md5Pwd = md5(this.loginPassword);
      let corpid = this.$store.getters.corpidValue; //this.$store.corpid;
      const obj = {};
      obj.loginName = this.loginName;
      obj.password = md5Pwd;
      obj.corpid = corpid;
      obj.deviceType = 1;
      let t = new Date(); //
      let t_s = t.getTime(); //
      let nt = new Date(); //
      nt.setTime(t_s + 5000 * 60); //5
      let refresh_expires=nt.getTime();
      obj.refresh_expires = refresh_expires;
      accountLogin(obj).then((res) => {
        if (res.success) {
          var storage = window.localStorage;
          storage.clear();
          storage.setItem("token", res.result.access_token);

          this.$store.commit({
            type: "updateToken",
            token: storage.token,
            corpid: res.result.corpid,
          });
          //
          console.log("");
          var corpid=this.$store.getters.corpidValue
          console.log(corpid);
           this.$router.push({ path: "/homepage", query: { corpid } });
        } else {
          console.log(res.resultMessage);
        }
      });
    }
  },
};
</script>

